@import "colors";
@import "mixins";

*, ::before, ::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  background: $primary;
  color: $secondary;
  font-family: Montserrat, sans-serif;
}

.container {
  margin: 0 auto;
  width: 80vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  h1 {
    font-size: 5rem;
    margin-bottom: 3rem;
  }

  button {
    @include triangle;
    position: relative;
    padding: 1.5rem 4rem;
    border-radius: 5px;
    outline: none;
    background: $primary;
    color: $secondary;
    border: 2px solid $secondary;
    cursor: pointer;
    font-size: 1.3rem;
    transition: ease-in .4s;
    margin-bottom: 3rem;

    &:hover {
      background: $secondary;
      color: $primary;
    }
  }

}

.container .finder {
  display: none;
  width: 50vw;
  background: $secondary;
  box-shadow: 2px 2px 10px ;
  color: $primary;
}